.post_detail {
    margin-bottom: 2em;
     
  }
  
  .post_detail img {
    max-width: 500px;
    margin-left: 100px;
    
  }
  
  .post_detail h2 {
    margin-bottom: 0.4em;
  }
  
  .post_detail .tags {
    margin-bottom: 1.2em;
    display: flex;
  }
  
  .tags p {
    margin-right: 0.5em;
  }
  
  .tags span {
    font-weight: bold;
  }
  
  .createdby {
    font-style: italic;
    color: #444;
    font-size: 0.8em;
    margin-bottom: 1em;
  }

  @media (max-width: 600px) {
    .post_detail img {
      flex-direction: column;
      
      width: 100%;
      height: 50%;
      margin-left: -5px;
    }
  }